<template>
    <div class="icons">
        <swiper :options="swiperOption" class="wrapper" v-if="showSwiper">
            <swiper-slide v-for="(page, index) of pages" :key="index">
                <div class="icon" v-for="item of page" :key="item.id">
                    <div class="icon-img">
                        <img :src="item.imgUrl" alt="">
                    </div>
                    <p>{{item.desc}}</p>
                </div>
            </swiper-slide>
            <div class="swiper-pagination pagination-self"  slot="pagination" v-if="ifshow"></div>
        </swiper>
    </div>
</template>
<script>
export default {
    name: 'HomeIcons',
    data(){
        return {
            swiperOption: {
                pagination: '.swiper-pagination'
            }
        }
    },
    props: {
        list: Array
    },
    computed: {
        pages(){
            let pages = [];
            this.list.forEach((item, index) => {
                let page = Math.floor(index / 8);
                if(!pages[page]){
                    pages[page] = [];
                }
                pages[page].push(item);
            })
            return pages
        },
        ifshow(){
            if(this.pages.length > 1){
                return true
            }else{
                return false
            }
        },
        showSwiper(){
            return this.list.length
        }
    }
}
</script>
<style lang="stylus" scoped>
    @import '~@/assets/styles/varibles.styl'
    @import '~@/assets/styles/mixins.styl'

    .icons
        overflow : hidden
        height : 3.7rem
        .wrapper
            position : relative
            height : 3.7rem
            .icon
                overflow : hidden
                float : left 
                width : 25%
                height : 1.65rem
                .icon-img
                    // position : relative
                    overflow hidden
                    height : 1.31rem
                    img
                        display : block
                        width : 1.1rem
                        margin : .15rem auto .05rem 
                        // position : absolute
                        // left : 50%
                        // top : 50%
                        // width: 1.1rem
                        // transform : translate(-50%, -50%)
                p
                    height : .3rem
                    line-height : .3rem
                    text-align : center
                    color : $darkTextColor
                    ellipsis()
            .pagination-self
                position : absolute
                top : 3.4rem
    </style>
