<template>
    <div>
        <div class="header">
            <router-link tag="div" to="/" class="header-left" @click.native="clearInput">
                <div class="iconfont back-icon">&#xe624;</div>
            </router-link>
            <div class="header-input">
                <input type="text" v-model="inputValue" placeholder="输入城市或景点" v-focus>
            </div>
            <div class="header-right">
                搜索
            </div>
        </div>
    </div>
</template>
<script>
import $ from 'jquery'
import { clearTimeout, setTimeout } from 'timers';

export default {
    name: 'SearchHeader',
    data(){
        return {
            inputValue: '',
            timer: null
        }
    },
    watch: {
        inputValue(){
            if(this.timer){
                clearTimeout(this.timer)
            }
            this.timer = setTimeout(() => {
                $.ajax({
                    type: 'GET',
                    url: `https://api.douban.com/v2/music/search?q=${this.inputValue}&count=10`,
                    dataType: 'jsonp',
                    success: this.addDom
                })
            },200) 
        }
    },
    methods: {
        addDom(data){
            this.$emit('addData', data.musics)
        },
        clearInput(){
            this.inputValue = ''
        }
    },
    directives: {
        focus: {
            inserted: function (el) {
                el.focus()
            }
        }
    }
}
</script>
<style lang="stylus" scoped>
    @import '~@/assets/styles/varibles.styl'
    .header
        display : flex
        line-height : $headerHeight
        .header-left
            width: .64rem
            .back-icon
                text-align : center
                font-size : .36rem
        .header-input
            flex : 1
            height : .64rem
            line-height : .64rem
            margin : .11rem 0
            padding : 0 .6rem 0 .2rem
            background : #f2f2f2
            border-radius : .3rem
            color : #212121
            input
                display: block;
                width: 100%;
                height: .4rem;
                line-height: .4rem;
                padding: .1rem 0;
                background: #f2f2f2;
                border: 0;
                font-size: .28rem;
                border-radius: .06rem;
                text-align: center;
        .header-right
            width : .8rem
            padding : 0 .1rem
            text-align : center
</style>