<template>
    <div class="container" @click="handleGallaryClick">
        <div class="wrapper">
            <swiper :options="swiperOption">
                <swiper-slide v-for="(item, index) of imgs" :key="index">
                    <img class="gallary-img" :src="item" alt="">
                </swiper-slide>
                <div class="swiper-pagination"  slot="pagination"></div>
            </swiper>
        </div>
    </div>
</template>
<script>
export default {
    name: 'CommonGallary',
    data(){
        return {
            swiperOption: {
                pagination: '.swiper-pagination',
                paginationType: 'fraction',
                observeParents: true,
                observer: true
            }
        }
    },
    props: {
        imgs: Array
    },
    methods: {
        handleGallaryClick(){
            this.$emit('close')
        }
    }
}
</script>
<style lang="stylus" scoped>
    .wrapper >>> .swiper-container
        overflow : inherit
    .container
        display : flex
        flex-direction : column
        justify-content : center
        z-index : 99
        position : fixed
        left : 0
        top : 0
        right : 0
        bottom : 0
        background : #000
        .wrapper
            width : 100%
            height : 0
            padding-bottom : 68.57%
            .gallary-img
                width : 100%
            .swiper-pagination
                color : #fff
                bottom : -1rem
</style>
