<template>
    <div>
        <div class="banner" @click="handleBannerClick">
            <img class="banner-img" :src="bannerImg" alt="">
            <div class="banner-info">
                <div class="banner-title">{{sightName}}</div>
                <div class="banner-number">
                    <span class="iconfont banner-icon">&#xe694;</span>
                    {{this.gallaryImgs.length}}
                </div>
            </div>
        </div>
        <fade-animation>
            <common-gallary :imgs="gallaryImgs" v-if="showGallary" @close="handleGallaryClose"></common-gallary>
        </fade-animation>
    </div>
</template>
<script>
import CommonGallary from '@/common/gallary/Gallary'
import FadeAnimation from '@/common/fade/FadeAnimation'
export default {
    name: 'DetailBanner',
    props: {
        sightName: String,
        bannerImg: String,
        gallaryImgs: Array
    },
    data(){
        return {
            showGallary: false,
        }
    },
    methods: {
        handleBannerClick(){
            this.showGallary = true;
        },
        handleGallaryClose(){
            this.showGallary = false;
        }
    },
    components: {
        CommonGallary,
        FadeAnimation
    }
}
</script>
<style lang="stylus" scoped>
    .banner
        position : relative
        overflow : hidden
        height : 0
        padding-bottom : 55%
        .banner-img
            width : 100%
        .banner-info
            display : flex
            position : absolute 
            left : 0
            right : 0
            bottom : 0
            line-height : .6rem
            color : #fff
            background-image : linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, .8))
            .banner-title
                flex : 1
                font-size : .32rem
                padding : 0 .2rem
            .banner-number
                height : .4rem
                line-height : .4rem
                padding : 0 .3rem
                margin-top : .1rem
                font-size : .24rem
                border-radius : .2rem
                background : rgba(0, 0, 0, .8)
                .banner-icon
                    font-size : .24rem
</style>
